@import '../sass/index.scss';

body {
  display           : flex;
  flex-direction    : column;
  // justify-content: space-between;
  height            : 100vh;
  font-family       : 'Dosis', sans-serif;
}

main {
  display        : flex;
  justify-content: center;
}

.card {
  margin: 0px;
  display: grid;
  grid-template-rows: repeat(5, auto);
  grid-template-areas: 
    "image"
    "title"
    "description"
    "published"
    "button";

  width  : 300px;


  &__image {
    width : auto;
    height: 200px;

    &--img {
      object-fit: cover;
      width     : 100%;
      height    : 100%;

    }
  }


}